import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { KnockOffComponent } from './knock-off.component';
import { KnockOffDetailComponent } from './knock-off-detail/knock-off-detail.component';

import { ACTION } from 'src/app/enums/action.enum';


const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'knock_off',
        pathMatch: 'full'
      },
      {
        path: "",
        component: KnockOffComponent,

        data: {
          title: "Knock Off",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
        }
      },
      {
        path: "knock_off_detail",
        component: KnockOffDetailComponent,

        data: {
          title: "Knock Off",
          permission: [
            ACTION.VIEW,
            ACTION.STATUS
          ],
        }
        // canDeactivate: [FormGuard],
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class KnockOffRoutingModule { }
