import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from 'src/app/shared/shared.module';
import { KnockOffRoutingModule } from './knock-off-routing.module';
import { KnockOffComponent } from './knock-off.component';
import { KnockOffDetailComponent } from './knock-off-detail/knock-off-detail.component';


@NgModule({
  declarations: [
    KnockOffComponent,
    KnockOffDetailComponent
  ],
  imports: [
    CommonModule,
    KnockOffRoutingModule,
    SharedModule
  ]
})
export class KnockOffModule { }
