import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import { MatTableDataSource } from '@angular/material/table';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';
import { FormArray, FormControl } from '@angular/forms';
import { BaseForm } from 'src/app/class/base.form';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './knock-off-detail.component.html',
  styleUrls: ['./knock-off-detail.component.css']
})
export class KnockOffDetailComponent extends BaseForm implements OnInit {

  public _dcoId : string;
  public _ou : string;
  public _dco_receipt : string;
  public _pId : string;
  public _deposit_slip : string;
  public dataSource = new MatTableDataSource();
  public editData : any = [];
  public displayedColumns = ['S No', 'Select', 'Receipt No', 'Amount' , 'Purpose','Donation Type' ,'Donation Subtype'];
  componentName: string;

  constructor(
    public injector: Injector,
    public activeRoute: ActivatedRoute,
  ) {
    super(injector);
  }

  ngOnInit(): void {
    this._fhs._pathLocation = '/knock_off';
    this.componentName = "Knock Off Edit";
    this._dcoId = this.activeRoute.snapshot.paramMap.get('dcoId');
    this._ou = this.activeRoute.snapshot.paramMap.get('ou');
    this._dco_receipt = this.activeRoute.snapshot.paramMap.get('dcoReceipt');
    this._pId = this.activeRoute.snapshot.paramMap.get('pId');
    this.initForm();
    this.getEditdata(this._dcoId);
  }

  initForm() {
    this._fs._form = this._fb.group({
      dco_id:[''],
      eraseed_ids: this._fb.array([]),
    });
  }

  getEditdata(dcoId){
    this._fs._form.get('dco_id').patchValue(dcoId);
    const selectedPerArr: FormArray = this._fs._form.get('eraseed_ids') as FormArray;
    this._http.get({
      url: environment.API_URL,
      endpoint: URLz.KNOCK_OFF,
      query: {
        id: dcoId,
      }
    }).subscribe((res : any)=>{
        this.dataSource.data = res.data.records;
        this.editData = res.data.records;
        res.data.records.forEach(val => {
          if(val.checked){
            selectedPerArr.push(new FormControl(val.id));
          }
        });
    })
  }

  getcheckBoxValue(event, eId){
    const selectedPerArr: FormArray = this._fs._form.get('eraseed_ids') as FormArray;
    if(event.target.checked){
      selectedPerArr.push(new FormControl(eId));
    }
    else{
      let i = 0;
      selectedPerArr.controls.forEach((ctrl: FormControl) => {
        if(ctrl.value == eId) {
          selectedPerArr.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  switch(){
    this._router.navigate(['/knock_off', {ou: this._ou, dcoReceipt: this._dco_receipt , pId: this._pId}]);
  }

  _onSubmit() {
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    if (this._fs._form.valid) {
      const modify  = this._http.update({body: this._fs._form.value, url: environment.API_URL , endpoint: URLz.KNOCK_OFF});
      modify.subscribe({
       next:(res: any) => {
          Swal.fire({
            title: 'KnockedOff',
            text: res.message,
            icon: 'success',
            confirmButtonColor: '#3085d6',
            confirmButtonText: '<i class="fas fa-thumbs-up"></i>',
          }).then((res) => {
            this._fs._form.reset();
            this.switch();
          });
        },
       error:(httpErrorResponse: HttpErrorResponse) => {
          this._vs._error_server(httpErrorResponse.error);
        }
      });
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }


}
